body {
  overflow: hidden;
}

:global {
  .corgi-mode-container {
    float: left;
    width: 100%;
    overflow: none;
    position: fixed;
    bottom: 0;
    height: 60px;
  }

  .helper {
    height: 60px;
    position: absolute;
    bottom: 0;
    display: inline-block;
  }

  .ltor-helper {
    animation: run-ltor 8s linear forwards;
  }

  .rtol-helper {
    animation: run-rtol 8s linear forwards;
    transform: scaleX(-1);
  }

  @keyframes :global(run-ltor) {
    0% {
      top: 10;
      left: -50vw;
    }
    100% {
      top: 10;
      left: 100vw;
    }
  }

  @keyframes :global(run-rtol) {
    0% {
      top: 10;
      left: 100vw;
    }
    100% {
      top: 10;
      left: -50vw;
    }
  }
}
